.Track {
	display: flex;
	align-items: center;
	border-bottom: 1px solid rgba(256, 256, 256, 0.8);
}

.Track-action {
	cursor: pointer;
	padding: 0.5rem;
	font-size: 1.05rem;
	transition: color 0.25s;
	border: 0px;
	background-color: rgba(0, 0, 0, 0);
	color: #fff;
	outline: none;
}

.PlayPause {
	width: fit-content;
	padding: 0;
	margin-top: 5px;
}

.Pause {
	color: red;
}

.NoAudio {
	margin-top: 5px;
	opacity: 0.5;
}

.Track-action:hover {
	color: rgba(265, 265, 265, 0.5);
}

.Track-information {
	flex-grow: 1;
	display: flex;
	flex-direction: column;
	justify-content: center;
	height: 72px;
}

.Track-information h3 {
	margin-bottom: 0.22rem;
}

.Track-information p {
	font-size: 0.83rem;
	font-weight: 300;
	color: rgba(256, 256, 256, 0.8);
}

img {
	margin: 0.5rem 0.5rem 0.5rem 0;
	height: 64px;
	width: 64px;
	object-fit: contain;
}

audio {
	margin-top: 0.5rem;
}
